import React,{ memo, useCallback, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {getMovieRank} from '../slices/MovieRankSlice';
import styled from 'styled-components';

import Spinner from '../components/Spinner';
import ErrorView from '../components/ErrorView';
import Table from '../components/Table';
//   
import BarChartView from '../components/BarChartView';
//      hook
import useMountedRef from '../hook/useMountedRef'

//   dayjs
import dayjs from 'dayjs';

//    flex-box
const Container = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

//      flex-basis: 50% 
  .flex-item{
      flex-basis: 50%;
      box-sizing: border-box;
      padding: 10px;
  }
`;

const MovieRank = memo(() =>{
    //dayjs().add(-1, 'd'). format('YYYY-MM-DD')     console.log
    /* console.log(`{dayjs().add(-1, 'd').format('YYYY-MM-DD')}`) */
    const dispatch = useDispatch();
     // Redux Store  Ajax  
    const {data, loading, error} = useSelector((state) => state.movieRank);
                                            //         
    const [targetDt, setTargetDt] = useState(dayjs().add(-1, 'd').format('YYYY-MM-DD'));

     //        hook
     const mountedRef = useMountedRef();
     
     //   
     const [chartData, setChartData]= useState();

    //           --> Ajax 
    React.useEffect(()=>{
        //targetDt     console.log 
        /* console.log('targetDt=${targetDt}') */
                                //    'YYYY-MM-DD'  - 
        dispatch(getMovieRank({targetDt: targetDt.replaceAll("-","")}));
    }, [dispatch, targetDt]);

    //     
   const onDateChange = useCallback((e)=>{
    e.preventDefault();
    //   . --> React.useEffect()    .
    setTargetDt(e.target.value);
   }, [setTargetDt]);

     
    //Ajax       chartData  .
    //Ajax      ,        .
    React.useEffect(()=>{
        //       .
        if (mountedRef.current){
            const newData = {
                movieNm: [],
                audiCnt:[]  
            };
            data.boxOfficeResult.dailyBoxOfficeList.forEach((v,i)=>{
                newData.movieNm.push(v.movieNm);
                newData.audiCnt.push(v.audiCnt);
            });
            setChartData(newData);
        }
    },[mountedRef,data]);

    
    return (
      <div>
          <Spinner visible={loading}/>

            <form>
                <input type='date' className='form-control' placeholder=' ' value={targetDt} onChange={onDateChange} />
            </form>
            
            <hr/>

          {error ? <ErrorView error={error}/>:(
              <Container>
                <div className='flex-item'>
                    <BarChartView chartData={chartData} />
                </div>
            <div className='flex-item'>        
              <Table>
                  <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th> </th>
                        <th></th>
                        <th>  </th>
                        <th> </th>
                    </tr>
                </thead>
                <tbody>
                    {data && data.boxOfficeResult.dailyBoxOfficeList.map((v,i)=>{
                        return (
                            <tr key={i}>
                                <td>{v.rank}</td>
                                    <td>{v.movieNm}</td>
                                    <td>{Number(v.audiCnt).toLocaleString()}</td>
                                    <td>{Number(v.salesAmt).toLocaleString()}</td>
                                    <td>{Number(v.audiAcc).toLocaleString()}</td>
                                    <td>{Number(v.salesAcc).toLocaleString()}</td>
                            </tr>
                        )
                    })}
                </tbody>
              </Table>
              </div>
              </Container>
          )}
      </div>
  );
});

export default MovieRank;